export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(id: string) {
        this.divHost = document.getElementById(id);

        // Center align
        // this.divHost.style.display = 'flex';
        // this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        // div.style.marginLeft = 'auto';
        // div.style.marginRight = 'auto';
        // // div.style.width = '600px';
        // // div.style.height = '350px';

        this.divHost.appendChild(div);

        // TODO
        // You code goes here for one time operation

        this.renderer = new Renderer(div);

        // !! you should execute it for rendering
        // this.start();
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

const loadModel0 = async () => {
    const modelName = 'ade20k';   // set to your preferred model, either `pascal`, `cityscapes` or `ade20k`
    const quantizationBytes = 2;  // either 1, 2 or 4
    return await deeplab.load({base: modelName, quantizationBytes});
};

const loadModel1 = async () => {
    const base = 'pascal';        // set to your preferred model, out of `pascal`,
                                  // `cityscapes` and `ade20k`
    const quantizationBytes = 2;  // either 1, 2 or 4
    // use the getURL utility function to get the URL to the pre-trained weights
    const modelUrl = deeplab.getURL(base, quantizationBytes);
    const rawModel = await tfconv.loadGraphModel(modelUrl);
    const modelName = 'pascal';  // set to your preferred model, out of `pascal`,
    // `cityscapes` and `ade20k`
    return new deeplab.SemanticSegmentation(rawModel);
};

